.app{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 40px 24px;
}

.searchForm{
  margin-top: 18px;
}

.textBox{
  padding: 6px 16px;
  min-width: 200px;
  max-width: 100vw;
  font-size: 15px;
}

.submit{
   padding: 6px 16px;
   font-size: 15px;
   margin-left: 6px;
   background-color: plum;
   border: 2px solid plum;
   border-radius: 4px;
   color:white;
}

.submit:hover{
  cursor:grab;
}

.appGrid{
  margin-top: 23px;
  padding: 20px;
  display: grid;
  grid-template-columns: auto auto ;
  text-align: center;
  background: linear-gradient(to right, lavender ,white, rgb(253, 216, 228));
}

.dropDown{
  padding: 7px 8px;
  margin-left: 6px;
  border-radius: 4px;
  font-size: medium;
}

.introduction{
  padding: 20px;
}


@media only screen and (max-width:560px){
  .appGrid{
    margin-top: 23px;
    padding: 20px;
    display: grid;
    grid-template-columns: auto;
  }

  .dropDown{
    margin-top: 10px;
  }
}