.foodItem:hover{
    cursor: pointer;
}
.foodItem{
    text-align: center;
}
.imgFoodItem{
    height: 200px;
    width: 200px;
    object-fit: cover;
    border-radius: 4px;
}

.recipeName{
    max-width: 200px;
    margin: 12px 0px;
    font-weight: bold;
}

.food{
    margin: 20px;
    max-width: 200px;
    border: solid 1px rgb(208, 205, 239);
}

.ingreButton{
    width: 150px;
    height: 30px;
    margin: 15px;
    background: cornsilk;
    border: 2px solid lightseagreen;
    border-radius: 4px;
    font-size: medium;
}

.ingreButton:hover{
    cursor:grab
}

.link{
    text-decoration: none;
    color: rgb(81, 24, 81);
    border: solid 1px rgb(81, 24, 81);
    padding: 2px;
}